import React, { useContext, useState, useEffect } from 'react';
import Fade from 'react-reveal/Fade';
import { Container, Row, Col } from 'react-bootstrap';
import { I18nextProvider, useTranslation } from 'react-i18next';
import Title from '../Title/Title';
import AboutImg from '../Image/AboutImg';
import PortfolioContext from '../../context/context';
import i18next from '../../i18n/config';

const About = () => {
  const { t } = useTranslation();
  const { about } = useContext(PortfolioContext);
  const { img, resume } = about;

  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <I18nextProvider i18n={i18next}>
      <section id="about">
        <Container>
          <Title title={t('aboutData.aboutMe')} />
          <Row className="about-wrapper">
            <Col md={6} sm={12}>
              <Fade bottom duration={1000} delay={600} distance="30px">
                <div className="about-wrapper__image">
                  <AboutImg alt="profile picture" filename={img} />
                </div>
              </Fade>
            </Col>
            <Col md={6} sm={12}>
              <Fade left={isDesktop} bottom={isMobile} duration={1000} delay={1000} distance="30px">
                <div className="about-wrapper__info">
                  <p className="about-wrapper__info-text">{t('aboutData.paragraphOne')}</p>
                  <p className="about-wrapper__info-text">{t('aboutData.paragraphTwo')}</p>
                  <p className="about-wrapper__info-text">{t('aboutData.paragraphThree')}</p>
                  {resume && (
                    <span className="d-flex mt-3">
                      <a
                        target="_blank"
                        rel="noopener noreferrer"
                        className="cta-btn cta-btn--resume"
                        href={t('aboutData.resume')}
                      >
                        {t('aboutData.resumeButton')}
                      </a>
                    </span>
                  )}
                </div>
              </Fade>
            </Col>
          </Row>
        </Container>
      </section>
    </I18nextProvider>
  );
};

export default About;
