import React, { useState } from 'react';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import { makeStyles } from '@material-ui/core/styles';
// import i18next from "./../../i18n/config";
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles(theme => ({
  formControl: {
    alignItems: 'center',
    marginLeft: theme.spacing(2),
  },
  selectEmpty: {
    '& .MuiSelect-root': {
      background: 'transparent',
    },
    marginLeft: theme.spacing(2),
  },
}));

const LanguageMenu = () => {
  const classes = useStyles();

  const { i18n } = useTranslation();

  const [values, setValues] = useState({
    language: 'fr',
  });

  function handleChange(event) {
    i18n.changeLanguage(event.target.value);
    setValues(oldValues => ({
      ...oldValues,
      [event.target.name]: event.target.value,
    }));
  }

  return (
    <FormControl className={classes.formControl}>
      <Select
        value={values.language}
        onChange={handleChange}
        displayEmpty
        autoWidth
        disableUnderline
        IconComponent={() => ''}
        className={classes.selectEmpty}
        inputProps={{ name: 'language' }}
      >
        <MenuItem value="fr">
          <img
            src="https://res.cloudinary.com/dkyqbngya/image/upload/c_scale,w_30/v1585483466/france-flag-button-round-icon-64_uqllrc.png"
            alt="French Flag"
          />
        </MenuItem>
        <MenuItem value="en">
          <img
            src="https://res.cloudinary.com/dkyqbngya/image/upload/c_scale,w_30/v1585483470/united-states-of-america-flag-button-round-xs_xgcefz.png"
            alt="US Flag"
          />
        </MenuItem>
      </Select>
    </FormControl>
  );
};

export default LanguageMenu;
